import { useRef, useLayoutEffect, useCallback } from 'preact/hooks';
import {
  convertUseAnimationCssPropertiesToRegularCSS,
  convertUseAnimationCssPropertiesToWAAPIKeyframe,
  UseAnimationCssProperties
} from './animationUtils';

export type UseAnimationConfig<V extends string, E extends HTMLElement> = {
  animationStates: Partial<
    Record<AnimationStateKey<V>, ((node: E) => AnimationConfig) | AnimationConfig>
  >;
  isAnimatedOnMount?: boolean;
  onAnimationEnd?: ({ animationState }: { animationState: V }) => void;
};

// Used to filter simple states and ones that include previous state
type AnimationStateKey<PA extends string> = ExtractAnimationStates<PA | `${PA} => ${PA}`>;

// Used to get previous and current states and do deeper typing
type ExtractAnimationStates<StateString extends string> =
  StateString extends `${infer From} => ${infer To}`
    ? KeyWithPreviousAndCurrentState<From, To>
    : StateString;

// Used to just allow keys with different previous and current states
type KeyWithPreviousAndCurrentState<PreviousState extends string, CurrentState extends string> = {
  b: PreviousState;
} extends { b: CurrentState }
  ? never
  : `${PreviousState} => ${CurrentState}`;

type AnimationConfig = {
  from?: UseAnimationCssProperties;
  to: UseAnimationCssProperties;
  end?: UseAnimationCssProperties;
  options?: AnimationOptions;
};

type AnimationOptions = {
  delay?: number;
  duration?: number;
  easing?:
    | 'linear'
    | 'ease'
    | 'ease-in'
    | 'ease-out'
    | 'ease-in-out'
    | [number, number, number, number];
};

// Used to exclude states that include => since this string is used to represent a transition from state to another.
type PermittedAnimationState<V extends string> = Exclude<V, `${string}=>${string}`>;

/**
 * Hook to animate single components.
 * It allows n number of animation states.
 * @param animationState
 * @param animationConfig
 * @returns
 */
export function useAnimation<V extends string, E extends HTMLElement = HTMLElement>(
  animationState: PermittedAnimationState<V>,
  {
    animationStates,
    isAnimatedOnMount = false,
    onAnimationEnd = () => {}
  }: UseAnimationConfig<PermittedAnimationState<V>, E>
) {
  const didMountRef = useRef(false);
  const nodeRef = useRef<E>(null);
  const currentAnimationState = useRef<PermittedAnimationState<V>>();
  const currentAnimation = useRef<Animation>();

  // We don't allow changing animationStates. Ref to keep "first run" animation states.
  const animationStatesRef =
    useRef<UseAnimationConfig<PermittedAnimationState<V>, E>['animationStates']>(animationStates);

  useLayoutEffect(() => {
    stopAnimation(currentAnimation.current);

    const animatedElement = nodeRef.current;
    if (!animatedElement) {
      return;
    }

    const previousAnimationState = currentAnimationState.current;
    currentAnimationState.current = animationState;

    const animationConfig = getConfig(
      animationStatesRef.current,
      animatedElement,
      animationState,
      previousAnimationState
    );

    if (!animationConfig) {
      return;
    }

    if (!didMountRef.current) {
      didMountRef.current = true;
      if (!isAnimatedOnMount) {
        setEndStyle(animatedElement, true, animationConfig);
        return;
      }
    }

    currentAnimation.current = startAnimation(
      animationConfig,
      animatedElement,
      animationState,
      onAnimationEnd
    );
  }, [animationState]);

  /**
   * Used to cancel current animation. When animation is canceled node style goes back to style of last finished animation
   * @returns
   */
  const cancelCurrentAnimation = useCallback(() => {
    const animation = currentAnimation.current;
    if (animation?.playState === 'running') {
      animation.currentTime = 0;
      stopAnimation(animation);
    }
  }, []);

  return { nodeRef, controller: { cancel: cancelCurrentAnimation } };
}

/**
 * Stops an animation.
 *
 * @param animation Animation that is going to be stopped.
 * @returns
 */
function stopAnimation(animation?: Animation) {
  if (animation) {
    // TO DO: rollup-plugin-typescript seems to have an outdated Animation typing. That produces warnings while bulding.
    // Update picked type to get rid of this casting.
    (animation as any).commitStyles();
    animation.cancel();
  }
}

/**
 * Gets animation configuration.
 *
 * @param animationStates Set of animation configurations provided by the user.
 * @param animatedElement Element that is going to be styled.
 * @param animationState Current animation state.
 * @param previousAnimationState Previous animation state.
 * @returns AnimationConfig.
 */
function getConfig<V extends string, E extends HTMLElement>(
  animationStates: UseAnimationConfig<PermittedAnimationState<V>, E>['animationStates'],
  animatedElement: E,
  animationState: PermittedAnimationState<V>,
  previousAnimationState?: PermittedAnimationState<V>
) {
  const currentAnimationStateConfig =
    animationStates[
      `${previousAnimationState} => ${animationState}` as keyof UseAnimationConfig<
        PermittedAnimationState<V>,
        E
      >['animationStates']
    ] ||
    animationStates[
      animationState as keyof UseAnimationConfig<PermittedAnimationState<V>, E>['animationStates']
    ];

  const animationConfig =
    typeof currentAnimationStateConfig === 'function'
      ? currentAnimationStateConfig(animatedElement)
      : (currentAnimationStateConfig as AnimationConfig);

  return animationConfig && Object.keys(animationConfig).length > 0 ? animationConfig : null;
}

/**
 * Starts an animation.
 *
 * @param animationConfig Animation configuration used by the animation.
 * @param animatedElement Element that is going to be styled.
 * @param animationState Current animation state.
 * @param onAnimationEnd Callback called once the animation is completed.
 * @returns Animation.
 */
function startAnimation<V extends string, E extends HTMLElement>(
  animationConfig: AnimationConfig,
  animatedElement: E,
  animationState: PermittedAnimationState<V>,
  onAnimationEnd: UseAnimationConfig<PermittedAnimationState<V>, E>['onAnimationEnd']
) {
  const { delay, duration, easing } = animationConfig.options || {};

  //Now, if user does not pass a value we override it by hand. We will use theming tools in future.
  const animationOptions = {
    delay: delay || 0,
    duration: duration || 400,
    easing: easing
      ? typeof easing === 'string'
        ? easing
        : `cubic-bezier(${easing[0]}, ${easing[1]}, ${easing[2]}, ${easing[3]})`
      : 'ease'
  };

  // WAAPI animation method. https://developer.mozilla.org/en-US/docs/Web/API/Element/animate
  // TO DO: Check for required browser compatibility in case we need pollyfill.
  // https://github.com/web-animations/web-animations-js/blob/dev/docs/support.md#browser-support
  const startedAnimation = animatedElement.animate(
    [
      convertUseAnimationCssPropertiesToWAAPIKeyframe(animationConfig.from),
      convertUseAnimationCssPropertiesToWAAPIKeyframe(animationConfig.to)
    ],
    { ...animationOptions, fill: 'forwards' }
  );

  startedAnimation.onfinish = () => {
    //We commit styles to have an only source of truth.
    stopAnimation(startedAnimation);

    //In case user pass a final style after animation we do that here.
    setEndStyle(animatedElement, false, animationConfig);

    // On animationEnd is only called when animation actually finished. If an animation is interruped by other one
    // the old one will not be finished. Do we want to have a callback for those cases?
    onAnimationEnd?.({ animationState });
  };

  return startedAnimation;
}

/**
 * Sets element style.
 *
 * @param animatedElement Element that is going to be styled.
 * @param toAndEndStyle Represents which data will be used to set style. If set to true it uses "to" and "endStyle".
 * If set to false it uses just "endStyle".
 * @param animationConfig Style that is going to be set.
 */
function setEndStyle<E extends HTMLElement>(
  animatedElement: E,
  toAndEndStyle: boolean,
  animationConfig: AnimationConfig
) {
  const endStyle = toAndEndStyle
    ? {
        ...convertUseAnimationCssPropertiesToRegularCSS(animationConfig.to),
        ...convertUseAnimationCssPropertiesToRegularCSS(animationConfig.end)
      }
    : convertUseAnimationCssPropertiesToRegularCSS(animationConfig.end);
  for (const key in endStyle) {
    //TO DO: Look for a better way to handle types.
    (<any>animatedElement.style)[key] = endStyle[key as keyof typeof endStyle];
  }
}
